@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=BioRhyme:wght@700&family=Montserrat:wght@300;400;500;600&display=swap');

body{
  margin: 0;
  padding: 0;
  background-color: #000266;
  font-family: 'Montserrat', 'BioRhyme', calibri;
}

*{
  box-sizing: border-box;
}
ul{
  list-style: none;
}
a{
  text-decoration: none;
}
input{
  outline: none;
  border: none;
}
.logo{
  font-family:'BioRhyme' ;
  color: #242424;
}
.main{
  width: 100%;
  height: 768px;
  position: relative;
  background: no-repeat;
  background-size: 27%;
  background-position: 800px 170px;
  background-image: url("./Images/portrait.jpg");
}
nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 5px 10px 30px rgba(0,0,0,0.4);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 1;
  background-color: #ffffff;
}
nav ul{
  display: flex;
}
nav ul li a{
  font-family: calibri;
  height: 40px;
  line-height: 43px;
  margin: 3px;
  padding: 0px 2px;
  display: flex;
  font-size: 0.8rem;
  text-transform: uppercase;
  font-weight: 700;
  color: #242424;
  letter-spacing: 1px;
  border-radius: 2px;
  transition: 0.4s ease-in-out;
}
.Hey{
  font-family: calibri;
  color: #000266;
  font-weight: 500;
  font-size: 0.9rem;
  border-bottom: 2px solid #000266;
}
nav ul li a:hover{
  background-color: #000266;
  color: #fff;
  box-shadow: 5px 10px 30px rgba(64,88,198,0.4);
}
nav .menu-btn,.menu-icon{
  display: none;
}
.name{
  font-family: 'Montserrat';
  width: 600px;
  position: absolute;
  left: 16%;
  top: 50%;
  transform: translate(-20%, -50%);
}
.name span{
  color: #d0ff00;
  /*border-bottom: 2px solid #d0ff00;*/
}
.name .details{
  color: #c7c7c7;
}
.name h2{
  font-weight: bold;
  color: #e9e9e9;
}
.name h1{
  font-family: 'Montserrat';
  font-size: 46px;
  margin: 0px;
  letter-spacing: 3px;
  color: lightpink;
}

.header-btns{
  display: flex;
  margin-top: 40px;
  font-weight: 600;
}
.btn-1{
  width: 110px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #d0ff00;
  box-shadow: 5px 10px 30px rgba(255, 251, 2, 0.2);
  border-radius: 10px;
  color: #000266;
}
.btn-2{
  font-size: 14px;
  width: 140px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: 2px solid lightpink;
  margin-left: 20px;
  border-radius: 10px;
  color: lightpink;
}
.btn-1:hover{
  background-color: #fff;
  transition: all ease 0.4s;
  color: #000266;
}
.btn-2:hover{
  background-color: #fff;
  transition: all ease 0.4s;
  color: #000266;
}
.about{
  width: 100%;
  height: 170vh;
  background-color: #ffffff;
}
.about h1{
  font-family: 'Montserrat';
  font-size: 46px;
  text-align: center;
  padding-top: 100px;
  letter-spacing: 1px;
  color: #000266;
}
/*for flip cards*/
.card-row {
  display: flex;
  justify-content: center;
}

.flip-card {
  background-color: #f1f1f1;
  width: 300px;
  height: 300px;
  perspective: 1000px;
  display: inline-block;
  margin: 10px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

.flip-card-front {
  background-color: #bbb;
  color: black;
  z-index: 2;
}

.flip-card-back {
  background-color: #FFE5EC;
  color: black;
  transform: rotateY(180deg);
  z-index: 1;
}
